.roadmap {
  padding: 0;
}

.roadmap__content {
  padding-bottom: 100px;
}

.roadmap__headcontainer {
  margin: 0px !important;
  padding: 0px !important;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(76, 32, 115, 1)), to(rgba(8, 159, 195, 1)));
  background: -webkit-linear-gradient(top, rgba(76, 32, 115, 1) 0%, rgba(8, 159, 195, 1) 100%);
  background: -o-linear-gradient(top, rgba(76, 32, 115, 1) 0%, rgba(8, 159, 195, 1) 100%);
  background: linear-gradient(180deg, rgba(76, 32, 115, 1) 0%, rgba(8, 159, 195, 1) 100%);
}

.roadmap__title {
  color: #fff;
  font-weight: 600;
  font-size: 32px;
}

.roadmap__buttons {
  margin: 80px auto 0 auto;
  width: 541px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.roadmap__button {
  width: 166.7px;
}

.roadmap__map {
  margin: 70px auto 0 auto;
  position: relative;
  width: 580px;
}

.roadmap__line {
  top: 14px;
  left: 6px;
  position: absolute;
  width: 567px;
  height: 2px;
  background-color: #fff;
}
.roadmap__circles {
  margin-top: 28px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.roadmap__circle {
  cursor: pointer;
  border-radius: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 30px;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.2);
}

.roadmap__text {
  width: 620px;
  font-size: 16px;
  color: white;
  font-weight: normal;
  margin: 0 auto;
  margin-top: 50px;
}
.roadmap__image {
  display: block;
  margin: 0 auto;
  margin-top: 50px;
  width: 600px;
  height: 500px;
  background-color: white
}

.roadmap__disc {
  border-radius: 100%;
  background-color: #fff;
  width: 20px;
  height: 20px;
}

.roadmap__year {
  font-size: 16px;
  color: #fff;
  font-weight: 300;
  margin-left: -5px;
  margin-right: -5px;
}

.roadmap__quartal {
  font-family: Geometria;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
}

.roadmap__quartals {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 5px;
  width: 100%;
  height: 16px;
}

.roadmap__years {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  height: 16px;
}

.roadmap__quartal_1 {
  margin-left: 5px;
}
.roadmap__quartal_9 {
  margin-right: 5px;
}

@media screen and (min-width: 1024px) {
  .roadmap {
    padding-left: 42px;
    padding-right: 42px;
  }
  .roadmap__buttons {
    margin: 0;
    margin-top: 85px;
    margin-right: 40px;
  }
  .roadmap__header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
  }
  .roadmap__map {
    width: 647px;
  }
  .roadmap__line {
    width: 600px;
    left: 25px;
  }
  .roadmap__text {
    width: 620px;
  }
  .roadmap__image {
    width: 620px;
    height: 500px;
  }
}

@media screen and (min-width: 1440px) {
  .roadmap__map {
    width: 904px;
  }
  .roadmap__line {
    width: 860px;
  }
  .roadmap__text {
    width: 800px;
  }
  .roadmap__image {
    width: 904px;
    height: 600px;
  }
}



