.block_9
{
  background: url(../../images/block_9_bg.png) no-repeat center;
  background-size: cover;
  padding-bottom: 90px;
  display: table;
  width: 100%;
}

.block_9 * {
  color: white;
}


.block_9 a.sb
{
  background : -webkit-linear-gradient(0deg, rgba(242, 135, 183, 1) 0%, rgba(108, 94, 214, 1) 100%);
  background : -webkit-gradient(linear,0% 50% ,100% 50% ,color-stop(0,rgba(242, 135, 183, 1) ),color-stop(1,rgba(108, 94, 214, 1) ));
  background : -o-linear-gradient(0deg, rgba(242, 135, 183, 1) 0%, rgba(108, 94, 214, 1) 100%);
  background : -ms-linear-gradient(0deg, rgba(242, 135, 183, 1) 0%, rgba(108, 94, 214, 1) 100%);
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#F287B7', endColorstr='#6C5ED6' ,GradientType=0)";
  background : -webkit-gradient(linear, left top, right top, from(rgba(242, 135, 183, 1)), to(rgba(108, 94, 214, 1)));
  background : -webkit-linear-gradient(left, rgba(242, 135, 183, 1) 0%, rgba(108, 94, 214, 1) 100%);
  background : -o-linear-gradient(left, rgba(242, 135, 183, 1) 0%, rgba(108, 94, 214, 1) 100%);
  background : linear-gradient(90deg, rgba(242, 135, 183, 1) 0%, rgba(108, 94, 214, 1) 100%);
  width : 163px;
  border-radius : 24px;
  -moz-border-radius : 24px;
  -webkit-border-radius : 24px;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#F287B7',endColorstr='#6C5ED6' , GradientType=1);
  display: inline-block;
  -webkit-box-shadow: 6px 7px 22px rgba(0, 0, 0, 0.3);
          box-shadow: 6px 7px 22px rgba(0, 0, 0, 0.3);
  text-align: center;
  font-size: 13px;
  font-weight: bold;
  line-height: 45px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  -webkit-transition: -webkit-box-shadow 0.3s;
  transition: -webkit-box-shadow 0.3s;
  -o-transition: box-shadow 0.3s;
  transition: box-shadow 0.3s;
  transition: box-shadow 0.3s, -webkit-box-shadow 0.3s;
  vertical-align: middle;
}

.block_9 a.sb:hover
{
  -webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.3);
          box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.3);
}

.block_9 a.sb_2
{
  width : 163px;
  border-radius : 24px;
  -moz-border-radius : 24px;
  -webkit-border-radius : 24px;
  display: inline-block;
  text-align: center;
  font-size: 13px;
  font-weight: bold;
  line-height: 41px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
  border: 1px solid #fff;
  margin-left: 20px;
  vertical-align: middle;
}

.block_9 a.sb_2:hover
{
 background: #fff;
 color: #000;
}

.block_9 .adress {
  width: 650px;
  margin: 60px 0;
}

.block_9 .adress_left {
  float: left;
}

.block_9 .adress_left p {
  margin: 15px 0;
  font-size: 16px;
  font-weight: 300;
}

.block_9 .adress_left .caption{
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 25px;
}

.block_9 .adress_right {
  float: right;
}

.block_9 .adress_right p {
  margin: 15px 0;
  font-size: 16px;
  font-weight: 300;
}

.block_9 .adress_right .caption {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 25px;
}

.block_9 .icon_block {
  width: 300px;
  margin-top: 50px;
}

.block_9 .icon_block a {
  cursor: pointer;
}

.block_9 .icon_block a .icon {
  width: 27px;
  height: 27px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.block_9 .container2 {
  margin-left: 117px;
  margin-top: 70px;
}

@media (max-width: 1439px) {
  .block_9 .container2 {
    margin: 70px 0 0;
  }
}



