.button {
  width: 100%;
  height: 40px;
  font-family: Geometria;
  font-weight: 600;
  border-radius: 30px;
  line-height: 40px;
  border: 1px solid #fff;
  text-align: center;
  color: #fff;
  cursor: pointer;
}

.button:hover {
  background-color: #fff;
  color: #6e5fd6;
}

.button_gradient {
  border: none;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#6e5fd6), to(#f086b8));
  background-image: -webkit-linear-gradient(bottom, #6e5fd6, #f086b8);
  background-image: -o-linear-gradient(bottom, #6e5fd6, #f086b8);
  background-image: linear-gradient(to top, #6e5fd6, #f086b8);
  -webkit-box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
}

.button_gradient:hover {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#6e5fd6), to(#f086b8));
  background-image: -webkit-linear-gradient(bottom, #6e5fd6, #f086b8);
  background-image: -o-linear-gradient(bottom, #6e5fd6, #f086b8);
  background-image: linear-gradient(to top, #6e5fd6, #f086b8);
  color: #fff;
}



